import React from "react";
import { useSelector } from "react-redux";
import { json, LoaderFunction, useLoaderData } from "remix";
import ClientController from "~/controllers/client";
import ConnectionController from "~/controllers/connection";
import GameController from "~/controllers/game";
import { AppState } from "~/stores";
import { printLog } from "~/utils/helper";

type GameData = {
  gameId: string;
};
export const loader: LoaderFunction = async ({ params }) => {
  printLog("info", "GAME", `joining room ID: ${params.gameId}`);
  return json({ ...params });
};

const GameScreen = () => {
  const socket = useSelector((state: AppState) => state.socket.client);
  const data = useLoaderData<GameData>();
  React.useEffect(() => {
    if (!socket) return;
    socket.emit("join", data.gameId);
  }, []);

  return <div>GameScreen</div>;
};

export default GameScreen;
